/* #region header */
.header {
  background-image: url(../images/header-img.png);
  background-size: cover;
  background-position: center;


  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1px;

    @include onTablet {
      font-size: 48px;
      line-height: 48px;
    }

    @include onDesktop {
      font-size: 56px;
      line-height: 56px;
    }
  }

  &__bottom {
    padding-bottom: 40px;

    @include onTablet {
      padding-bottom: 65px;
    }

    @include onDesktop {
      padding-bottom: 92px;
    }
  }
}

/* #endregion */